<template>
    <div class="container">
        <div class="login">
            <div class="card-body">
                <h2 class="card-title text-center" style="font-family: Times New Roman, Times, serif;" >Register</h2>
                <form @submit.prevent="register" class="text-center" id="form" ref="form">
                    <div class="form-group">
                        <input type="text" class="form-control" placeholder="Enter your name..." name="name" v-model="name"/>
                        <br/>
                        <input type="email" class="form-control" placeholder="Enter your email" name="email" v-model="email"/>
                        <br/>
                        <input type="password" class="form-control" placeholder="Enter your password" name="password" v-model="password"/>
                        <br/>
                        <p v-if="errorText" style="color:white;background-color: rgba(255, 0, 0, 0.8); ">{{errorText}}</p>
                     </div>
                     <button type="submit" class="btn btn-primary" style="margin-left: 5px;">Submit</button>
                </form>
                <br/>
                
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Register',
    data(){
        return{
            name: "",
            email:"",
            password:"",
            errorText: null,
        }
    },
     beforeCreate: function() {
        document.body.style.background = "url('https://images.unsplash.com/photo-1577563908411-5077b6dc7624?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80')";
        document.body.style.backgroundSize ="cover";
        document.body.style.backgroundPosition ="center";
        document.body.style.backgroundRepeat =" no-repeat";
        document.body.style.backgroundSize = "cover";
     },
    methods:{
        register(){
            if (this.name && this.email && this.password.length >= 6){
                this.$store.dispatch('userJoin', {
                    email: this.email,
                    password: this.password,
                    name: this.name
                });
                }
            else{
                this.errorText = "Please Enter your name AND your email, password atleast 6 "
            }
        },
        
    },
    
}
</script>
<style>
    .login{
        max-width: 450px;
        margin: 50px auto;
        display: block;
    }
</style>