import Vue from "vue";
import Vuex from "vuex";
import firebase from 'firebase';
import router from '@/router';
Vue.use(Vuex);

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: null,
    isAuthenticated: false,
  },
  mutations: {
    setUser(state, payload) {
      state.user = payload;
  },
      setIsAuthenticated(state, payload) {
      state.isAuthenticated = payload;
  }
  },
  actions: {
    userJoin({ commit }, { email, password,name }) {
      firebase
          .auth()
          .createUserWithEmailAndPassword(email, password)
          .then( () => {
              firebase.database()
              .ref('users/' + firebase.auth().currentUser.uid + "/profile")
              .set({
                  Username: name,
                  UserEmail: email,
              })
              alert("sucessful register with the name: " + name)
              router.push('/')
           })
          .catch(() => {
              commit('setUser', null);
              commit('setIsAuthenticated', false);
              alert("Register fail, check Email or Password again !")
          });
  },
  userLogin({ commit }, { email, password }) {
    firebase
        .auth()
        .signInWithEmailAndPassword(email, password)
        .then(user => {
            var userId = firebase.auth().currentUser.uid;
            
            firebase.database().ref('/users/' + userId).once('value').then(snapshot=>{
            router.push({name:'chatwithparam', params:{name: snapshot.val().profile.Username, auth:true}})
            })
            commit('setUser', user);
            commit('setIsAuthenticated', true);
            
        })
        .catch(() => {
            commit('setUser', null);
            commit('setIsAuthenticated', false);
            alert("Login fail, check Email or Password again !")
        });
      
  }
  },
  modules: {}
});


