<template>
    <div class="container">
        <div class="login">
            <div class="card-body form">
                <h2 class="card-title text-center" style="font-family: Times New Roman, Times, serif;">Chat App Room</h2>
                <form @submit.prevent="login" class="text-center">
                    <div class="form-group">
                        <input type="email" class="form-control" placeholder="Please enter your email..." name="email" v-model="email"/>
                        <br/>
                         <input type="password" class="form-control" placeholder="Please enter your password..." name="password" v-model="password"/>
                         <br/>
                        <p v-if="errorText" style="color:white;background-color: rgba(255, 0, 0, 0.8);" >{{errorText}}</p>
                    </div>
                    <button type="submit" class="btn btn-primary" style="margin-right: 5px;">Submit</button>
                    <button class="btn btn-primary" style="margin-left: 5px;" @click="changeRoute('register')">Register</button>
                </form>
                <br/>
                
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'login',
    data(){
        return{
            email: "",
            password: "",
            errorText: null,
            auth: false,
        }
    },
    beforeCreate: function() {
        document.body.style.background = "url('https://images.unsplash.com/photo-1577563908411-5077b6dc7624?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80')";
        document.body.style.backgroundSize ="cover";
        document.body.style.backgroundPosition ="center";
        document.body.style.backgroundRepeat =" no-repeat";
        document.body.style.backgroundSize = "cover";
     },
    
     
    methods:{
        login(){
            if (this.email && this.password){
                this.$store.dispatch('userLogin', {
                email: this.email,
                password: this.password
                });
            
                
            }
            else{
                this.errorText = "Please Enter your name and password"
                
            }
        },
        changeRoute(val){
             this.$router.push({name:val})
        }
    },
}
</script>
<style scroped>
    .login{
        max-width: 450px;
        margin: 50px auto;
        display: block;
        color: Yellow;
    }
    .form{
      
    }
</style>